
import { Component, Input, OnInit } from '@angular/core';
import { collapse } from '../../../shared/animation/collapse-animate';
import { GlobalService } from 'src/app/service/template/global.service';
import { UsuarioService } from 'src/app/service/usuario/usuario.service';

@Component({
  selector: 'du-menu',
  templateUrl: './menu-principal.component.html',
  styleUrls: ['./menu-principal.component.scss'],
  animations: [collapse]

})
export class MenuPrincipalComponent  {

  autenticado:boolean;

  @Input() menuInfo: any;

  constructor(private _globalService: GlobalService,
    private usuarioService:UsuarioService) { }

    ngOnInit() {
    }

  private isToggleOn(item) {
    item.toggle === 'on' ? item.toggle = 'off' : item.toggle = 'on';
  }

  private _selectItem(item) {
    this._globalService._isActived(item);
    this._globalService.dataBusChanged('isActived', item);
  }

  getAutenticacao(){
    if(this.usuarioService.getAutenticado()==true){
    this.autenticado=true;
  } else {
    this.autenticado=false;

  }
  }
 
}
