import { Component, OnInit, Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Http } from '@angular/http';
import { FormControl } from '@angular/forms';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToasterService } from 'angular2-toaster';
import { MatDialog } from '@angular/material';
import { UsuarioService } from 'src/app/service/usuario/usuario.service';
import { Usuario } from 'src/app/models/usuario';
import { NgxUiLoaderService, Loader, SPINNER, POSITION, PB_DIRECTION } from 'ngx-ui-loader';
import { Alert } from 'selenium-webdriver';
import swal from 'sweetalert2';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
@Injectable()
export class LoginComponent implements OnInit {

  loginText = new FormControl('', []);
  senha = new FormControl('', []);
  private toasterService: ToasterService;
  @BlockUI() blockUI: NgBlockUI;
  spinner: SPINNER;
  loading = false;
  submitted = false;
  returnUrl: string;
  error = '';
  usuario:Usuario;
  usuarioAutenticado:boolean;
  
  constructor(
    public router: Router, 
    toasterService: ToasterService,
    public http: Http,
    private authenticationService: UsuarioService,
    private dialog: MatDialog,
    private ngxUiLoaderService: NgxUiLoaderService
  ) {
    this.toasterService = toasterService;
  }

  ngOnInit() {

    }
  
  login(){
    this.usuario = new Usuario();
    if(this.validarCampos()){
      var login = this.loginText.value;
      var senha = this.senha.value;
      if(login == null || login == '' || senha == null || senha == ''){
        this.alertWarning('Login e Senha são obrigatórios');
        return;
      }
      this.ngxUiLoaderService.start();
      this.authenticationService.autenticar(this.loginText.value, this.senha.value)
      .subscribe(
          result => {
            if (result != null) {
              this.usuario.login=result.login;
              this.ngxUiLoaderService.stop();
              this.authenticationService.getAutenticacao(true);
              this.authenticationService.getExibirMenu(true);
              this.authenticationService.getNomeUsuario(result.login);
              this.router.navigate(['home']);
            } else {
              this.ngxUiLoaderService.stop();
              this.alertError('Login ou Senha Inválidos, ou Usuário não Cadastrado');
            }
              
          },
          error => {
            this.authenticationService.getAutenticacao(false);
            this.authenticationService.getExibirMenu(false);
            this.error = error;
            this.loading = false;
            this.toasterService.pop('error', 'Login ou Senha Inválidos');
            this.ngxUiLoaderService.stop();
          });
    }
  }

  validarCampos(): boolean{
    var retorno = true;
    if(this.loginText.value == ''  && this.senha.value == '' ){
      this.loginText.setErrors(['']);
      this.alertWarning('Nome de Usuario e Senhas São Obrigatorios!');
      retorno = false;
      return retorno;
 
    }
    if(this.loginText.value == null || this.loginText.value == ''){
      this.loginText.setErrors(['']);
      this.alertWarning('Favor Insira o Nome do Usuario!');
      retorno = false;
      return retorno;

    }
    if(this.senha.value == null || this.senha.value == ''){
      this.senha.setErrors(['']);
      this.alertWarning('Favor Insira a Senha!');
      retorno = false;
      return retorno;

    }

   
    return retorno;
  }


  getUsuarioAutenticado(){

    return this.usuarioAutenticado;
  }



  
  alertSucesso(texto:string) {
    swal({
      type: 'success',
      text: texto,
    });
  }

  alertWarning(texto:string) {
    swal({
      type: 'warning',
      text: texto,
    });
  }

    
  alertError(texto:string) {
    swal({
      type: 'error',
      text: texto,
    });
  }

}
