import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './pages/login/login.component';
import { MaskUtil } from './utils/mask-util';
import { CadastroMembroComponent } from './pages/membros/cadastro/cadastro-membro/cadastro-membro.component';
import { PesquisaDetalhadaMembroComponent } from './pages/membros/pesquisa/pesquisa-detalhada-membro/pesquisa-detalhada-membro.component';
import { PesquisaMembroComponent } from './pages/membros/pesquisa/pesquisa-membro/pesquisa-membro.component';
import { DetalharMembroComponent } from './pages/membros/detalhar-membro/detalhar-membro.component';
import { HomeComponent} from './pages/home/home.component';
import { PesquisaGcComponent } from './pages/gc/pesquisa/pesquisa-gc/pesquisa-gc.component';
import { CadastroGcComponent } from './pages/gc/cadastro/cadastro-gc/cadastro-gc.component';
import { DadosComponent } from './pages/gc/cadastro/dados/dados.component';
import { DetalharGcComponent} from './pages/gc/detalhar-gc/detalhar-gc.component';
import { PagesTopComponent } from './pages/template/pages-top/pages-top.component';
import { RightConfigComponent } from './pages/template/right-config/right-config.component';
import { SidebarComponent } from './pages/template/sidebar/sidebar.component';
import {AuthGuard} from './guard/auth.guard';
import {CanActivate } from '@angular/router';
import { MenuPrincipalComponent } from './pages/template/menu-principal/menu-principal.component';


const routes: Routes = [
  // Padrão para telas com base no perfil
  { path: 'login', component: LoginComponent},
  { path: 'home', component: HomeComponent ,canActivate: [AuthGuard]},
  { path: 'cadastro-membro', component: CadastroMembroComponent,canActivate: [AuthGuard]},
  { path: 'pesquisa/pesquisa-membro', component: PesquisaMembroComponent, canActivate: [AuthGuard]},
  { path: 'pesquisa/pesquisa-membro-detalhada', component: PesquisaDetalhadaMembroComponent, canActivate: [AuthGuard]},
  { path: 'detalhar-membro/detalhar-membro', component: DetalharMembroComponent, canActivate: [AuthGuard]},
  { path: 'cadastro-gc', component: CadastroGcComponent, canActivate: [AuthGuard]},
  { path: 'pesquisa/pesquisa-gc', component: PesquisaGcComponent, canActivate: [AuthGuard]},
  { path: 'detalhar-gc/detalhar-gc', component: DetalharGcComponent,canActivate: [AuthGuard]},
  { path: 'PagesTop', component: PagesTopComponent,canActivate: [AuthGuard]},
  { path: 'RigthConfig', component: RightConfigComponent,canActivate: [AuthGuard]},
  { path: 'Side', component: SidebarComponent,canActivate: [AuthGuard]},
  { path: 'menu', component: MenuPrincipalComponent,canActivate: [AuthGuard]},
  { path: '**', redirectTo: 'login'}

  
];

@NgModule({
  exports: [RouterModule],
  imports: [RouterModule.forRoot(routes)],
  providers: [MaskUtil]
})
export class AppRoutingModule {}
