
import { DadosComponent } from '../dados/dados.component';
import { Component, OnInit, ViewChild, QueryList } from '@angular/core';
import { MatStepper, MatTableDataSource, MatPaginator } from '@angular/material';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { GcService } from 'src/app/service/gc/gc.service';
import { Gc } from 'src/app/models/gc';
import { ToasterService } from 'angular2-toaster';
import { NgxUiLoaderService, Loader, SPINNER, POSITION, PB_DIRECTION } from 'ngx-ui-loader';
import swal from 'sweetalert2';
import { Router, ActivatedRoute } from '@angular/router';



@Component({
  selector: 'app-cadastro-gc',
  templateUrl: './cadastro-gc.component.html',
  styleUrls: ['./cadastro-gc.component.css'],
})
export class CadastroGcComponent implements OnInit {

  @ViewChild('stepper') stepper: MatStepper;
  @ViewChild('dados') dados: DadosComponent;
  @BlockUI() blockUI: NgBlockUI;

  isOptional = false;
  gc: Gc;
  private toasterService: ToasterService;
  idGc:number;
  constructor(private gcService: GcService,
    private ngxUiLoaderService: NgxUiLoaderService,
    public router: Router, 
    private route: ActivatedRoute,


    ) {
  }

  ngOnInit() {

    this.route.queryParams.subscribe((queryParams: any) => { this.idGc = queryParams['id']; });
     if (this.idGc && this.idGc !== null) {
        this.ngxUiLoaderService.start();
        this.gcService.findById(this.idGc).subscribe(retorno => {
        this.carregarGc(retorno);
      });
      this.ngxUiLoaderService.stop();
    }

    

   }


  salvar() {
      this.ngxUiLoaderService.start();
      this.createGc();
      if(this.idGc==0 || this.idGc==null){
      this.gcService.save(this.gc).subscribe(data => {

        if (data == null) {
          //   console.log("NULL");
          this.alertErro('Erro ao Cadastrar GC!');
          this.ngxUiLoaderService.stop();
                  }
          
                  this.alertSucesso('GC Cadastrado Com Sucesso!');
                  this.router.navigate(['pesquisa/pesquisa-gc']);

                  this.ngxUiLoaderService.stop();
  });
    } else {
      this.gc.id = this.idGc;
      this.gcService.alterarGc(this.gc).subscribe(data => {

        if (data == false) {
          this.alertErro('Erro Alterar GC!');
          this.ngxUiLoaderService.stop();
                  }
          
                  this.alertSucesso('GC Alterado Com Sucesso!');
                  this.router.navigate(['pesquisa/pesquisa-gc']);

                  this.ngxUiLoaderService.stop();
    });


    }
      // }
      }

      private createGc() {

        this.gc = new Gc();

        //Dados Pessoais
        this.gc.nome = this.dados.dadosForm.controls['nome'].value;
        this.gc.diaSemana = this.dados.dadosForm.controls['diaSemana'].value;
        this.gc.logradouro = this.dados.dadosForm.controls['logradouro'].value;
        this.gc.zona = this.dados.dadosForm.controls['zona'].value;
        this.gc.horario = this.dados.dadosForm.controls['horario'].value;
        //Dados Lider GC
        this.gc.membroResponsavel = this.dados.dadosForm.controls['idMembroResponsavel'].value;
      }

  
  alertSucesso(texto:string) {
    swal({
      type: 'success',
      text: texto,
    });
  }

  alertErro(texto:string) {
    swal({
      type: 'error',
      text: texto,
    });
  }


  private carregarGc(gc: Gc) {

    this.gc = new Gc();
   
    // Dados GC
    this.idGc = gc.id;
    this.dados.dadosForm.get('nome').setValue(gc.nome);
    this.dados.dadosForm.get('diaSemana').setValue(gc.diaSemana);
    this.dados.dadosForm.get('logradouro').setValue(gc.logradouro);
    this.dados.dadosForm.get('zona').setValue(gc.zona);
    this.dados.dadosForm.get('horario').setValue(gc.horario);
    
  }
  }