import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { NgxUiLoaderService, Loader, SPINNER, POSITION, PB_DIRECTION } from 'ngx-ui-loader';
import { MembroService } from 'src/app/service/membro/membro.service';
import { GcService } from 'src/app/service/gc/gc.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],

})
export class HomeComponent implements OnInit {

  showloading: boolean = false;
  qtdMembros:number;
  qtdGc:number;
  maiorGc:any;
  membroSemGc:any;

  @BlockUI() blockUI: NgBlockUI;

  public AnimationBarOption;

  constructor(public router: Router,
    private ngxUiLoaderService: NgxUiLoaderService,
    private membroService:MembroService,
    private gcService:GcService
    ) {
  }

  ngOnInit() {
  this.getQuantidadeMembros();
  this. getQuantidadeGc();
  this.getMaiorGc();
  this.getMembroSemGc();
  }


  getQuantidadeMembros(){

    this.membroService.getQuantidadeMembros().subscribe(retorno => {
        this.qtdMembros=retorno;
      });
  }

  getQuantidadeGc(){

    this.gcService.getQuantidadeGc().subscribe(retorno => {
        this.qtdGc=retorno;
      });
  }

  getMaiorGc(){
    this.gcService.getMaiorGc().subscribe(result => {
        this.maiorGc=result;
      });

  }

  getMembroSemGc(){
    this.membroService.getMembroSemGc().subscribe(result => {
        this.membroSemGc=result;
      });

  }
}
